<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background }">
    <side-bar></side-bar>
    <side-bar-right></side-bar-right>
    <v-container>
      <v-toolbar color="rgba(0,0,0,0)" flat>
        <!-- ---------------------------- Label For Category ---------------------------- -->
        <v-toolbar-title class="mt-n3">Choose Category</v-toolbar-title>
        <v-spacer></v-spacer>
        <!-- ---------------------------- Category Search Bar ---------------------------- -->
        <v-text-field
          label="Search category or menu..."
          class="mt-5"
          color="primary"
          filled
          append-icon="mdi-magnify"
          dense
          solo
          flat
          background-color="grey lighten-4"
        ></v-text-field>
        <!-- ---------------------------- End Category Search Bar ---------------------------- -->
      </v-toolbar>
      <!-- ---------------------------- Category Menu ---------------------------- -->
      <v-item-group mandatory class="mt-n1">
        <v-container>
          <v-row justify="center" class="space">
            <!-- ---------------------------- For All Category ---------------------------- -->

            <v-col cols="12" sm="2" @click="onClickAllCategories()">
              <v-item v-slot="{ active, toggle }">
                <v-card
                  :color="active ? '#F6EFEF' : 'white'"
                  :class="active ? 'borderme' : 'borderout'"
                  class="d-flex align-center rounded-lg mx-2"
                  dark
                  height="100"
                  @click="toggle"
                  flat
                >
                  <v-row>
                    <v-col cols="12" sm="12">
                      <v-list-item three-line class="text-center">
                        <v-list-item-content>
                          <div align="center" justify="center">
                            <v-img
                              src="c1.png"
                              max-height="110"
                              max-width="110"
                              contain
                            ></v-img>
                          </div>
                          <v-list-item-subtitle
                            :class="active ? 'primary--text' : 'black--text'"
                            class="caption mt-4"
                            >ALL</v-list-item-subtitle
                          >
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                  </v-row>
                </v-card>
              </v-item>
            </v-col>
            <!-- ----------------------------End For All Category ---------------------------- -->

            <!-- ---------------------------- Category Items ---------------------------- -->

            <v-col
              cols="12"
              sm="2"
              v-for="(category, index) in categoryList"
              :key="index"
              @click="onClickCategory(category)"
            >
              <v-item v-slot="{ active, toggle }">
                <v-card
                  :color="active ? '#F6EFEF' : 'white'"
                  :class="active ? 'borderme' : 'borderout'"
                  class="d-flex align-center rounded-lg mx-2"
                  dark
                  height="100"
                  @click="toggle"
                  flat
                >
                  <v-row>
                    <v-col cols="12" sm="12">
                      <v-list-item three-line class="text-center">
                        <v-list-item-content>
                          <div align="center" justify="center">
                            <v-img
                              src="c1.png"
                              max-height="110"
                              max-width="110"
                              contain
                            ></v-img>
                          </div>
                          <v-list-item-subtitle
                            :class="active ? 'primary--text' : 'black--text'"
                            class="caption mt-4"
                            >{{ category.categoryName }}</v-list-item-subtitle
                          >
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                  </v-row>
                </v-card>
              </v-item>
            </v-col>

            <!-- ---------------------------- End Category Items ---------------------------- -->
          </v-row>
        </v-container>
      </v-item-group>

      <!-- ---------------------------- Label For Item List By Category ---------------------------- -->
      <v-toolbar color="#EEEEEE" flat>
        <v-toolbar-title>Coffee Menu</v-toolbar-title><v-spacer></v-spacer
        ><span color="grey">12 Coffees Results</span>
      </v-toolbar>
      <!-- ---------------------------- Show Item List By Category ---------------------------- -->
      <v-item-group mandatory class="mt-n1">
        <v-container>
          <v-row justify="center" class="space">
            <!-- ---------------------------- Item List ---------------------------- -->
            <v-col
              cols="12"
              md="2"
              v-for="(item, index) in foodList"
              :key="index"
            >
              <v-item v-slot="{ active, toggle }">
                <v-card
                  :color="active ? '#F6EFEF' : 'white'"
                  :class="active ? 'borderme' : 'borderout'"
                  class="d-flex align-center rounded-lg mx-2"
                  dark
                  height="140"
                  @click="toggle"
                  flat
                >
                  <v-row>
                    <v-col cols="12" sm="12">
                      <v-list-item three-line class="text-center">
                        <v-list-item-content>
                          <div align="center" justify="center">
                            <v-img
                              src="c1.png"
                              max-height="110"
                              max-width="110"
                              contain
                            ></v-img>
                          </div>
                          <v-list-item-subtitle
                            :class="active ? 'primary--text' : 'black--text'"
                            class="caption mt-4"
                            >{{ item.foodName }} <br /><strong
                              >{{ item.foodPrice }} Ks</strong
                            ></v-list-item-subtitle
                          >
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                  </v-row>
                </v-card>
              </v-item>
            </v-col>
            <!-- ----------------------------End Item List ---------------------------- -->
          </v-row>
        </v-container>
      </v-item-group>
    </v-container>
  </v-app>
</template>

<script>
import SideBar from "../components/SideBar";
import SideBarRight from "../components/SideBarRight";
import api from "@/util/api";
export default {
  name: "HomeView",
  components: {
    SideBar,
    SideBarRight,
  },
  data() {
    return {
      categoryList: [],
      foodList: [],
    };
  },
  async created() {
    await this.fetchAllCategories();
    await this.fetchAllFoods();
  },
  methods: {
    async fetchAllCategories() {
      const response = await api.get("/category/list");
      if (response.status === 200) {
        const data = await response.json();
        this.categoryList = data;
      }
    },
    async fetchAllFoods() {
      const response = await api.get("/food/list");
      if (response.status === 200) {
        const data = await response.json();
        this.foodList = data;
      }
    },
    async onClickCategory(category) {
      const response = await api.get("/food/category/" + category.categoryId);
      if (response.status === 200) {
        const data = await response.json();
        if (data) {
          this.foodList = data;
        }
      }
    },
    async onClickAllCategories() {
      const response = await api.get("/food/list");
      if (response.status === 200) {
        const data = await response.json();
        this.foodList = data;
      }
    },
  },
};
</script>
