<template>
  <v-navigation-drawer
    v-model="drawer"
    app
    color="white"
    mini-variant
    mini-variant-width="90"
  >
    <v-avatar
      v-if="!isLogin"
      class="d-block text-center mx-auto mt-4"
      size="40"
    >
      <v-icon color="primary">fas fa-shop</v-icon>
    </v-avatar>
    <v-list flat class="mt-4">
      <v-list-item-group v-model="selectedItem">
        <v-list-item
          v-for="(item, i) in items"
          :key="i"
          active-class="border"
          :ripple="false"
          :to="item.route"
        >
          <v-list-item-content>
            <v-icon v-text="item.icon"></v-icon>
            <v-list-item-subtitle
              align="center"
              v-text="item.text"
              class="mt-3 caption"
            ></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
    <v-spacer></v-spacer>

    <div
      v-if="isLogin"
      :ripple="false"
      style="
        bottom: 20px;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        text-align: center;
      "
    >
      <a class="a-logout" href="" @click="logout()">
        <v-icon>fas fa-sign-out-alt</v-icon><br /><span class="caption"
          >Logout</span
        ></a
      >
    </div>
  </v-navigation-drawer>
</template>

<script>
export default {
  data: () => ({
    loginUser: {},
    isLogin: false,
    selectedItem: 0,
    drawer: null,
    items: [
      { icon: "fas fa-chair", text: "Table", route: "/admin/user" },
      { icon: "fas fa-hamburger", text: "Menu", route: "/admin/category" },
      { icon: "fas fa-list", text: "Order,", route: "/admin/food" },
      { icon: "fas fa-wallet", text: "Wallet", route: "/admin/table" },
    ],
  }),

  created() {
    this.loginUser = this.$store.state.loginUser;
    this.$store.watch(
      () => {
        return this.$store.state.loginUser;
      },

      {
        deep: true,
      }
    );
    this.isLogin = this.$store.state.isLogin;
    this.$store.watch(
      () => {
        return this.$store.state.isLogin;
      },

      {
        deep: true,
      }
    );
  },

  methods: {
    logout() {
      this.$store.commit("logout");
      if (this.$route.path != "/") {
        this.$router.push({ path: "/" });
      }
    },
  },
};
</script>

<style>
.sidebar-nav-list {
  text-decoration: none;
}
.border {
  margin-left: 8px;
  margin-right: 8px;
  background: #800015;
  border-radius: 20px;
  text-decoration: none;
}
.v-list-item-group .v-list-item--active,
.sidebar-nav-list {
  color: #ffffff !important;
}
.theme--light.v-list-item--active .v-list-item__subtitle,
.theme--light.v-list-item .v-list-item__action-text {
  color: #ffffff !important;
}
.a-logout {
  text-decoration: none;
}
</style>
