<template>
  <v-app>
    <admin-side-bar></admin-side-bar>
    <template>
      <v-data-table :headers="headers" :items="foodList" class="elevation-1">
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>My CRUD</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  New Item
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6">
                        <v-text-field
                          label="Food Name"
                          v-model="editedItem.foodName"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                        <v-select
                          v-model="categoryName"
                          :items="categoryList"
                          item-text="categoryName"
                          item-value="id"
                          :rules="[(v) => !!v || 'Required']"
                          label="Category"
                          required
                        >
                        </v-select>
                      </v-col>
                    </v-row>

                    <v-row>
                      <v-col cols="12" sm="6">
                        <v-text-field
                          v-model="editedItem.foodPrice"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                        <v-text-field
                          label="Price"
                          v-model="editedItem.foodPrice"
                        ></v-text-field>
                      </v-col>
                    </v-row>

                    <v-checkbox
                      v-model="editedItem.isAvaliable"
                      value="editedItem.isAvaliable"
                      label="Avaliable"
                    ></v-checkbox>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancel
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5"
                  >Are you sure you want to delete this item?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancel</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
        </template>
      </v-data-table>
    </template>
  </v-app>
</template>
<script>
import AdminSideBar from "@/components/AdminSideBar.vue";
import api from "@/util/api";
export default {
  name: "AdminView",
  components: {
    AdminSideBar,
  },
  data() {
    return {
      search: "",
      dialog: false,
      dialogDelete: false,
      headers: [
        { text: "ID", value: "foodId" },
        { text: "Food Name", value: "foodName" },
        { text: "Category", value: "foodCategory.categoryName" },
        { text: "Price", value: "foodPrice" },
        { text: "Actions", value: "actions", sortable: false },
      ],
      categoryList: [],
      foodList: [],

      editedIndex: -1,

      editedItem: {
        foodName: "",
        categoryName: "",
        categoryList: [],
        foodPrice: 0,
        isAvaliable: true,
      },
      defaultItem: {
        foodName: "",
        categoryName: "",
        categoryList: [],
        foodPrice: 0,
        isAvaliable: true,
      },
    };
  },
  async created() {
    await this.fetchAllCategories();
    await this.fetchAllFoods();
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    // foods() {
    //   return this.$store.state.foods;
    // },
  },
  mounted() {
    //this.$store.dispatch("getAllFoods");
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    async fetchAllCategories() {
      const response = await api.get("/category/list");
      if (response.status === 200) {
        const data = await response.json();
        this.categoryList = data;
      }
    },
    async fetchAllFoods() {
      const response = await api.get("/food/list");
      if (response.status === 200) {
        const data = await response.json();
        this.foodList = data;
      }
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    },
  },
};
</script>

<style>
.add-food {
  background-color: #800015 !important;
  color: #ffffff !important;
}
</style>
