import Vue from "vue";
import VueRouter from "vue-router";
import TableView from "../views/TableView.vue";
import RegisterView from "../views/RegisterView.vue";
import MenuView from "../views/MenuView.vue";
import OrderView from "../views/OrderView.vue";
import ProfileView from "../views/ProfileView.vue";
import AdminView from "../views/admin/AdminView.vue";
import AdminUserView from "../views/admin/AdminUserView.vue";
import AdminCategoryView from "../views/admin/AdminCategoryView.vue";
import AdminFoodView from "../views/admin/AdminFoodView.vue";
import AdminTableView from "../views/admin/AdminTableView.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "register",
    component: RegisterView,
  },
  {
    path: "/table",
    name: "table",
    component: TableView,
  },
  {
    path: "/menu",
    name: "menu",
    component: MenuView,
  },
  {
    path: "/profile",
    name: "profile",
    component: ProfileView,
  },
  {
    path: "/order",
    name: "order",
    component: OrderView,
  },
  {
    path: "/admin",
    name: "admin",
    component: AdminView,
  },
  {
    path: "/admin/user",
    name: "userList",
    component: AdminUserView,
  },
  {
    path: "/admin/category",
    name: "categorylist",
    component: AdminCategoryView,
  },
  {
    path: "/admin/table",
    name: "tablelist",
    component: AdminTableView,
  },
  {
    path: "/admin",
    name: "foodList",
    component: AdminFoodView,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
